import React from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

const Home = ({ lang }) => {
  return (
    <div className='card-panel center hoverable' style={{ marginTop: '15%' }}>
      <h4 className='center grey-text text-darken-1'>
        {lang === 'en' ? (
          <div>
            <p>Hello, I'm Alex.</p>
            <p>I'm web developer from Minsk, Belarus.</p>
          </div>
        ) : (
          <div>
            <p>Привет, меня зовут Александр.</p>
            <p>Я веб-разработчик из Минска, Беларусь.</p>
          </div>
        )}
        <Link to='/projects' className='waves-effect waves-light btn-large'>
          <i className='material-icons right'>trending_flat</i>
          {lang === 'en' ? 'my projects' : 'мои работы'}
        </Link>
      </h4>
    </div>
  );
};

Home.propTypes = {
  lang: PropTypes.string.isRequired
};

export default Home;
