import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import logo from './logo/3.png';

const Navbar = ({ lang, switchLang }) => {
  return (
    <Fragment>
      <nav>
        <div className='nav-wrapper'>
          <Link to='/' className='brand-logo'>
            <img className='hoverable logo' alt='' src={logo} />
          </Link>
          <Link to='#' data-target='mobile-demo' className='sidenav-trigger'>
            <i className='material-icons'>menu</i>
          </Link>
          <ul id='nav-mobile' className='right hide-on-med-and-down'>
            <li>
              <Link to='/'>{lang === 'en' ? 'Home' : 'Домой'}</Link>
            </li>
            <li>
              <Link to='/projects'>
                {lang === 'en' ? 'Projects' : 'Работы'}
              </Link>
            </li>
            <li>
              <a
                href='http://github.com/yalalex/'
                rel='noopener noreferrer'
                target='_blank'
              >
                Github
              </a>
            </li>
          </ul>
          <ul id='nav-mobile' className='right'>
            <li>
              <a onClick={switchLang}>{lang === 'en' ? 'RU' : 'EN'}</a>
            </li>
          </ul>
        </div>
      </nav>
      <ul className='sidenav' id='mobile-demo'>
        <li>
          <Link to='/'>{lang === 'en' ? 'Home' : 'Домой'}</Link>
        </li>
        <li>
          <Link to='/projects'>{lang === 'en' ? 'Projects' : 'Работы'}</Link>
        </li>
        <li>
          <a
            href='http://github.com/yalalex/'
            rel='noopener noreferrer'
            target='_blank'
          >
            Github
          </a>
        </li>
      </ul>
    </Fragment>
  );
};

Navbar.propTypes = {
  lang: PropTypes.string.isRequired,
  switchLang: PropTypes.func.isRequired
};

export default Navbar;
